import { Component, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MyErrorStateMatcher } from 'src/app/Interfaces/errorMatcher';
import { ElementService } from 'src/app/services/element.service';

import { PeriodicElement } from '../../../Interfaces/periodicElement';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})

export class CreateComponent {

  formGroup: FormGroup;
  inProgress: boolean = false;
  matcher = new MyErrorStateMatcher();

  constructor(
    private elementService: ElementService,
    private formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<CreateComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PeriodicElement
  ) {
    this.formGroup = this.formBuilder.group({
      position: [this.data.position, Validators.required],
      name: [this.data.name, Validators.required],
      weight: [this.data.weight, Validators.required],
      symbol: [this.data.symbol, Validators.required]
    });
  }

  done() {
    if (this.formGroup.valid) {
      this.inProgress = true;
      this.elementService.create(this.formGroup.value).subscribe(list => {
        this.inProgress = false;
        this.dialogRef.close(list);
      });
    }
  }

  errorHandling = (control: string, error: string) => {
    return this.formGroup.controls[control].hasError(error);
  }

}
