import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { PeriodicElement } from 'src/app/Interfaces/periodicElement';
import { ElementService } from 'src/app/services/element.service';
import { UploadService } from 'src/app/services/upload.service';

@Component({
  selector: 'app-uploads',
  templateUrl: './uploads.component.html',
  styleUrls: ['./uploads.component.css']
})
export class UploadsComponent implements OnInit {

  displayedColumns: string[] = ['name', 'status', 'timestamp'];
  dataSource!: MatTableDataSource<PeriodicElement>;
  data: PeriodicElement[] = [];
  
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(
    private elementService: ElementService,
    private uploadService: UploadService
  ) { }

  ngOnInit() {
    this.uploadService.list().subscribe(list => {
      this.setDataSource(list)
    })
  }

  doFilter(value: String) {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }


  setDataSource(list: PeriodicElement[]) {
    /* update only if list is being returned */
    if (typeof (list) == "object") {

      /* Used for calculating max position number*/
      this.data = list;

      /* used for material table **/
      this.dataSource = new MatTableDataSource(list);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  
  upload(event: any) {
    
    if(event.target.files.length){
      this.elementService.upload(event.target.files[0]).subscribe();
    }
    
  }

}
