import { Component, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MyErrorStateMatcher } from 'src/app/Interfaces/errorMatcher';
import { ElementService } from 'src/app/services/element.service';

import { PeriodicElement } from '../../../Interfaces/periodicElement';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})

export class EditComponent {

  formGroup: FormGroup;
  matcher = new MyErrorStateMatcher();

  constructor(
    private elementService: ElementService,
    private formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<EditComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PeriodicElement
  ) {
    this.formGroup = this.formBuilder.group({
      position: [
        {
          value: this.data.position,
          disabled: true
        },
        Validators.required
      ],
      name: [
        {
          value: this.data.name,
          disabled: true
        },
        Validators.required
      ],
      weight: [
        {
          value:this.data.weight,
          disabled: false
        },
        Validators.required],
      symbol: [
        {
          value: this.data.symbol,
          disabled: true
        },
        Validators.required]
    });
  }

  done() {
    if (this.formGroup.valid) {
      this.data.weight = this.formGroup.value.weight;
      
      this.elementService.update(this.data).subscribe(list => {
        this.dialogRef.close(list);
      });
    }
  }

}
