import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';

import { PeriodicElement } from '../../Interfaces/periodicElement'
import { MatSort } from '@angular/material/sort';
import { CreateComponent } from './create/create.component';
import { ElementService } from 'src/app/services/element.service';
import { EditComponent } from './edit/edit.component';
import { DeleteComponent } from './delete/delete.component';

@Component({
  selector: 'app-list',
  templateUrl: './element.component.html',
  styleUrls: ['./element.component.css']
})

export class ElementComponent implements OnInit {

  displayedColumns: string[] = ['position', 'name', 'weight', 'symbol', 'action'];
  dataSource!: MatTableDataSource<PeriodicElement>;
  data: PeriodicElement[] = [];
  
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(
    private dialog: MatDialog,
    private elementService: ElementService
  ) { }

  ngOnInit() {
    this.elementService.list().subscribe(list => {
      this.setDataSource(list)
    })
  }

  doFilter(value: String) {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  add() {
    const dialogRef = this.dialog.open(CreateComponent, {
      data: {
        name: '',
        position: this.data.reduce((element, current) => (element.position < current.position ? current : element),{position:0}).position + 1,
        weight: 0,
        symbol: '',
      }
    });
    dialogRef.afterClosed().subscribe(list => {
      this.setDataSource(list)
    });
  }

  edit(element: PeriodicElement) {
    const dialogRef = this.dialog.open(EditComponent, {
      data: element
    });

    dialogRef.afterClosed().subscribe(list => {
      this.setDataSource(list)
    });
  }

  delete(element: PeriodicElement) {
    const dialogRef = this.dialog.open(DeleteComponent, {
      data: element
    });

    dialogRef.afterClosed().subscribe(list => {
      this.setDataSource(list)
    });
  }

  setDataSource(list: PeriodicElement[]) {
    /* update only if list is being returned */
    if (typeof (list) == "object") {

      /* Used for calculating max position number*/
      this.data = list;

      /* used for material table **/
      this.dataSource = new MatTableDataSource(list);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  upload(event: any) {
    
    if(event.target.files.length){
      this.elementService.upload(event.target.files[0]).subscribe( _ => {
        setTimeout(()=>{
          this.elementService.list().subscribe(list => {
            this.setDataSource(list)
          })
     }, 3000);
      });
    }
    
  }

}

interface HTMLInputEvent extends Event {
  target: HTMLInputElement & EventTarget;
}