import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PeriodicElement } from '../Interfaces/periodicElement';

@Injectable({
  providedIn: 'root'
})
export class ElementService {

  constructor(private http: HttpClient) { }

  list(): Observable<PeriodicElement[]> {
    return this.http.get<PeriodicElement[]>('http://localhost:8000/element/')
  }

  upload(file:any) {
    const formData = new FormData();
    formData.append('file', file);
    return this.http.post<any>('http://localhost:8000/upload/file/', formData)
  }

  create(element: PeriodicElement): Observable<PeriodicElement[]> {
    return this.http.post<PeriodicElement[]>('http://localhost:8000/element/create/', element)
  }

  update(element: PeriodicElement): Observable<PeriodicElement[]> {
    return this.http.put<PeriodicElement[]>(`http://localhost:8000/element/update/${element.id}/`, element)
  }

  delete(id: number) {
    return this.http.delete<PeriodicElement[]>(`http://localhost:8000/element/delete/${id}/`)
  }

}

